<template>
  <op-container>
    <op-sidebar :width="sidebarWidth"></op-sidebar>
    <op-body :headHeight="headHeight" :marginLeft="sidebarWidth">
      <router-view></router-view>
    </op-body>
  </op-container>
</template>

<script>
  import opContainer from '@/components/base/container.vue'
  import opSidebar from '@/components/base/sidebar.vue'
  import opBody from '@/components/base/opBody.vue'
  export default {
    name: 'monitor',
    data: function () {
      return {
        headHeight: '60px',
        sidebarWidth: '200px'
      }
    },
    components: {opContainer, opSidebar, opBody}
  }
</script>

<style lang="less"></style>



