import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
export default new Router({
  /* 后端支持可开 */
  // mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  base: '/monitor',
  routes: [
    {path: '/realtime', component: resolve => require(['components/monitor/realtime.vue'], resolve), meta: {nav: ['监控管理', '实时监控']}},
    {path: '/space', component: resolve => require(['components/monitor/space.vue'], resolve), meta: {nav: ['监控管理', '泊位监控']}}
  ]
})



